@font-face {
  font-family: "SpaceMission";
  src: url(/froggy-gaming/static/media/SpaceMission-rgyw9.a3ade876.otf);
}
html {
  font-size: 62, 5%;
}

body {
  background: #ddd;
}

.header {
  background: white;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin: 0 auto;
  height: 90px;
}
.header-invin-flex {
  opacity: 0;
  visibility: hidden;
  display: none;
}
.header-hamburger {
  display: none;
}
.header-hamburger-toggle {
  display: none;
}
.header-logo {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  flex-shrink: 0;
}
.header-icon {
  height: auto;
  width: 70px;
  vertical-align: middle;
  align-self: center;
  justify-self: center;
}
.header-brand {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  font-size: 2rem;
  padding-left: 0.7rem;
  font-family: "SpaceMission";
}
.header-navigation {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}
.header-navigation-list {
  list-style-type: none;
  flex-shrink: 1;
  margin-left: 1rem;
  display: flex;
  align-items: center;
}
.header-navigation-item {
  display: inline-block;
  font-size: 1.1rem;
  padding: 0.5rem 0;
  text-transform: uppercase;
  font-weight: 500;
  color: #0a0a0a;
  cursor: pointer;
  transition: all 200ms linear;
}
.header-navigation-item:hover {
  color: #6d9c00;
}
.header-navigation-item:not(:first-child) {
  margin-left: 0.9rem;
}
.header-navigation-item--divider {
  border-left: 2px solid rgba(0, 0, 0, 0.1);
  height: 30px;
  vertical-align: middle;
  position: relative;
  margin: 0 7.5px;
  cursor: default;
}
.header-navigation-item:nth-child(9) {
  position: relative;
}
.header-navigation-item:nth-child(9):hover .header-navigation-form {
  opacity: 1;
  visibility: visible;
  -webkit-transform: translateY(1rem);
          transform: translateY(1rem);
}
.header-navigation-item:nth-child(9)::after {
  content: "";
  position: absolute;
  width: 2.6rem;
  height: 1rem;
  top: 2.2rem;
  left: -1rem;
  cursor: default;
}
.header-navigation-cart {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  margin-left: 3rem;
}
.header-navigation-cart:hover .header-navigation-cart-number {
  background: #6d9c00;
}
.header-navigation-cart-number {
  position: absolute;
  color: white;
  font-weight: 500;
  font-size: 0.7rem;
  position: absolute;
  background: #0a0a0a;
  border-radius: 50%;
  top: 0;
  left: 0;
  z-index: 1;
  width: 1.2rem;
  height: 1.2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 200ms linear;
}
.header-navigation-form {
  visibility: hidden;
  opacity: 0;
  position: absolute;
  background: white;
  transition: opacity 160ms linear, -webkit-transform 100ms linear;
  transition: opacity 160ms linear, transform 100ms linear;
  transition: opacity 160ms linear, transform 100ms linear, -webkit-transform 100ms linear;
  border: 2px solid #ddd;
  box-shadow: 1px 1px 15px rgba(0, 0, 0, 0.15);
  border-radius: 0.5rem;
  padding: 2rem 1rem;
  right: -0.5rem;
  cursor: default;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 350px;
  z-index: 4;
}
.header-navigation-form-find {
  position: relative;
  width: 100%;
  display: flex;
  align-items: center;
  z-index: 4;
}
.header-navigation-form-input {
  outline: none;
  border: 1px solid rgba(0, 0, 0, 0.03);
  border-radius: 1rem;
  padding: 1rem 1.5rem;
  max-width: 100%;
  width: 100%;
  height: 2rem;
  font-size: 1rem;
  background-color: rgba(0, 0, 0, 0.03);
  box-shadow: none;
  border-color: rgba(0, 0, 0, 0.09);
}
.header-navigation-form-input:-ms-input-placeholder {
  font-size: 1rem;
  font-weight: 400;
}
.header-navigation-form-input::-webkit-input-placeholder {
  font-size: 1rem;
  font-weight: 400;
}
.header-navigation-form-input::placeholder {
  font-size: 1rem;
  font-weight: 400;
}
.header-navigation-form-label {
  position: absolute;
  top: 0.5rem;
  left: 0.7rem;
  font-size: 0.85rem;
  text-transform: none;
  pointer-events: none;
  color: gray;
  font-weight: 500;
  width: 100%;
  height: 100%;
}

.triangle {
  position: absolute;
  width: 0;
  height: 0;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-bottom: 10px solid white;
  top: -0.45rem;
  right: 0.7rem;
}

@media screen and (max-width: 1205px) {
  .header-navigation-cart {
    margin-left: 2rem;
  }
}
@media screen and (max-width: 1188px) {
  .header-icon {
    width: 100px;
    height: auto;
  }
  .header-navigation-cart {
    margin-left: auto;
  }
  .header-brand {
    display: none;
  }
}
@media screen and (max-width: 987px) {
  .header {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .header-icon {
    width: 60px;
  }
  .header-navigation {
    display: none;
  }
  .header-navigation-list {
    display: none;
  }
  .header-brand {
    display: inline-flex;
    font-size: 1.2rem;
  }
  .header-invin-flex {
    opacity: 0;
    visibility: hidden;
    display: block;
  }
  .header-hamburger {
    display: block;
  }
  .header-hamburger-toggle {
    display: block;
    margin-left: 1rem;
  }
}/*# sourceMappingURL=Nav.css.map */
